<template>
  <!-- Menu -->
  <div>
    <mq-layout mq="tablet">
      <button
        v-if="menuOrContact"
        type="button"
        class="btn btn-link"
        @click="toggleButton()"
      >
        <i class="fa fa-bars fa-3x iconmenu" id="menuicon"></i>
      </button>

      <button
        v-if="!menuOrContact"
        type="button"
        class="btn btn-link"
        @click="toggleButton()"
      >
        <i class="fa fa-phone fa-3x iconmenu" id="phoneicon"></i>
      </button>

      <transition name="bounce" mode="out-in">
        <!-- Contactgegevens -->
        <app-contactmenu v-if="menuOrContact"></app-contactmenu>
        <!-- Menu navigatiebar -->
        <app-navbar v-if="!menuOrContact"></app-navbar>
      </transition>
    </mq-layout>
    <mq-layout mq="laptop+">
      <div id="navDesktop">
        <router-link to="/">Home</router-link>
        <router-link to="/about">Glow up</router-link>
        <router-link to="/makeup">Behandelingen</router-link>
        <router-link to="/arrangenworkshops"
          >Arrangementen &amp; Workshops</router-link
        >
        <router-link to="/fotos">Fotoalbum</router-link>
        <router-link to="/contact">Contact</router-link>
      </div>
      <!-- Contactgegevens -->
      <div class="col-md-6" style="margin: 0 auto">
        <app-contactmenu
          v-if="['contact'].indexOf($route.name)"
        ></app-contactmenu>
      </div>
    </mq-layout>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { mapGetters } from "vuex";
import NavbarVue from "./Navbar.vue";
import ContactmenuVue from "./Contactmenu.vue";
export default {
  data() {
    return {
      menuOrContact: true
    };
  },
  components: {
    appNavbar: NavbarVue,
    appContactmenu: ContactmenuVue
  },
  methods: {
    toggleButton() {
      this.menuOrContact = !this.menuOrContact;
    }
  }
};
</script>

<style>
/* Full menu */
#fullmenu {
  margin: 1rem;
}

.iconmenu {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  text-align: center;
  line-height: 80px;
  font-size: 36px;
}

#phoneicon {
  background: #ff8d5b;
  color: beige;
}

#menuicon {
  background: beige;
  color: #ff8d5b;
}

/* Desktop menu */
#navDesktop a {
  font-weight: bold;
  color: #eee;
  text-decoration: none;
  display: inline-block;
  min-height: 2rem;
  line-height: 2rem;
  margin: 0.8rem;
}
#navDesktop a.router-link-exact-active {
  color: #ff8d5b;
}

#navDesktop a:hover {
  color: #ff8d5b;
}

.hrtje {
  border: 1px solid white;
}

/*  slide fade transition 
    for menu contact buttons    */
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

/* bounce menu contact div */
.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

/* Media queries */
/* @media screen and (max-width: 992px) {
  #menuMobile {
    visibility: visible;
  }
  #menuDesktop {
    visibility: hidden;
  }
}

@media screen and (min-width: 992px) {
  #menuMobile {
    visibility: hidden;
  }
  #menuDesktop {
    visibility: visible;
  } */
</style>
