import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import Fotos from "./views/Fotos.vue";
import Behandelingen from "./views/Behandelingen.vue";
import Arrangenworkshops from "./views/Arrangenworkshops.vue";
import Arrangement from "./components/Arrang/Arrangement.vue";
import Workshop from "./components/Arrang/Workshop.vue";
import Contact from "./views/Contact.vue";
import BehandelingHair from "./components/Behandelingen/Hair/BehandelingenHair.vue";
import BehandelingNails from "./components/Behandelingen/Nails/BehandelingenNails.vue";
import BehandelingBrows from "./components/Behandelingen/Brows/BehandelingenBrows.vue";
import BehandelingMakeup from "./components/Behandelingen/Makeup/BehandelingenMakeup.vue";
import BehandelingSkin from "./components/Behandelingen/Skin/BehandelingenSkin.vue";
import BehandelingWedding from "./components/Behandelingen/Wedding/BehandelingenWedding.vue";
import BehandelingLashes from "./components/Behandelingen/Lashes/BehandelingenLashes.vue";
import BehandelingSugarwax from "./components/Behandelingen/Sugarwax/BehandelingenSugarwax.vue";
import BehandelingTan from "./components/Behandelingen/Tan/BehandelingenTan.vue";
import BehandelingAccessories from "./components/Behandelingen/Accessories/BeschrijvingAccessories.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "",
      name: "home",
      component: Home
    },
    {
      path: "/about",
      name: "about",
      component: About
    },
    {
      path: "/behandelingen",
      component: Behandelingen,
      children: [
        {
          path: "/hair",
          component: BehandelingHair,
          name: "behandelingHair"
        },
        {
          path: "/nails",
          component: BehandelingNails,
          name: "behandelingNails"
        },
        {
          path: "/skin",
          component: BehandelingSkin,
          name: "behandelingSkin"
        },
        {
          path: "/makeup",
          component: BehandelingMakeup,
          name: "behandelingMakeup"
        },
        {
          path: "/bridal",
          component: BehandelingWedding,
          name: "behandelingWedding"
        },
        {
          path: "/brows",
          component: BehandelingBrows,
          name: "behandelingBrows"
        },
        {
          path: "/lashes",
          component: BehandelingLashes,
          name: "behandelingLashes"
        },
        {
          path: "/sugarwax",
          component: BehandelingSugarwax,
          name: "behandelingSugarwax"
        },
        {
          path: "/tan",
          component: BehandelingTan,
          name: "behandelingTan"
        },
        {
          path: "/accessories",
          component: BehandelingAccessories,
          name: "behandelingAccessories"
        }
      ]
    },
    {
      path: "/arrangenworkshops",
      name: "arrangenworkshops",
      component: Arrangenworkshops,
      children: [
        {
          path: "/arrangementen",
          component: Arrangement,
          name: "arrangement"
        },
        {
          path: "/workshops",
          component: Workshop,
          name: "workshop"
        }
      ]
    },
    {
      path: "/fotos",
      name: "fotos",
      component: Fotos
    },
    {
      path: "/contact",
      name: "contact",
      component: Contact
    },
    {
      path: "/redirect",
      redirect: {
        name: "home"
      }
    },
    {
      path: "*",
      redirect: {
        path: "/"
      }
    }
  ]
});
